* {
  font-family: "나눔고딕", NanumGothic, "맑은고딕", MalgunGothic, "Apple SD Gothic Neo", sans-serif;
}
main{
  background-color:  #152e5c;
  margin-left: 35%
}
main > .movie{
  height: 100vh;
  padding: 12% 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color:white;
}
main > .movie > .wrapper{
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
main > .movie > .wrapper > .movie__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 25px;
}
main > .movie > .wrapper > .movie__header > h5{
  font-size: 1rem;
}
main > .movie > .wrapper > .movie__header > h6{
  font-size: 0.8remx;
}
main > .movie > .wrapper > .movie__video{
  width: 100%;
  height: 100%;
}

main > .movie > .wrapper > .movie__video > iframe{
  width: 100%;
  height: 100%;
}