/* red : #e7473c; */
header{
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f0eff0;
  width: 35%;
  min-width: 486px;
  height: 100%;
  padding: 12vh 0 72px 36px !important;
  display: flex !important;
  flex-direction: column;
  justify-content: space-between;
  color: #152e5c;
}
header > h1 {
  font-family: "나눔고딕", NanumGothic, "맑은고딕", MalgunGothic, "Apple SD Gothic Neo", sans-serif;
  font-size: 2.5rem;
  font-weight: 600;
  line-height: 1.5;
  width: 95%;
}
.logo {
  width: 414px;
  height: 80px;
  background-image: url("../images/3rd_ProBaduk.png");
  background-size: 410px 76px;
  border: 2px solid #152e5c;
  border-radius: 10px;
}
.bottom {
  padding: 50px 0 0 0;
  width: 100%
}
.bottom > p {
  font-family: "나눔고딕", NanumGothic, "맑은고딕", MalgunGothic, "Apple SD Gothic Neo", sans-serif;
  width: 420px;
  line-height: 1.5;
  justify-content: space-between;
  text-align: center;
}
.bottom > p {
  font-weight: 900;
  font-size: 2.25rem;
  margin-bottom: 40px;
  justify-content: center;
}
.ul_box {
  margin: 0 0 0 4px;
  border-radius: 10px;
  width: 410px;
  height: 120px;
  background-color: #d6dff0;
}
.bottom > nav > ul {
  padding: 21px 0;
  width: 420px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.bottom > nav > ul > li {
  font-family: "나눔고딕", NanumGothic, "맑은고딕", MalgunGothic, "Apple SD Gothic Neo", sans-serif;
  cursor: pointer;
  margin-right: 20px;
  font-size: 1.7rem;
  font-weight: 800;
  color: #152e5c;
  opacity: 0.75;
  margin-bottom: 20px;
  text-decoration: none;
}
.bottom > nav > ul > li:hover {
  color: #152e5c;
  opacity: 1;
}
.sheet {
  text-decoration: none;
  color: #152e5c;
}